import axios, { AxiosHeaders } from "axios";
import type { AxiosResponse, AxiosRequestConfig } from "axios";
import $config from "@/config/index";
import QS from "qs";
import store from "@/state/store";
import { Result } from "./type";

// axios.defaults.baseURLproxy
// if (process.env.NODE_ENV !== "development") {
//   axios.defaults.baseURL = $config.baseURL;
// }
axios.defaults.baseURL = $config.baseURL;
axios.defaults.headers["Content-Type"] = "application/json;charse=UTF-8";
axios.defaults.timeout = 30000; // 

//
axios.interceptors.request.use(
  (config: any) => {
    config.headers.Authorization = store.getState().user.access_token;
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);
//
axios.interceptors.response.use(
  (response) => {
    if (response.data.status) {
      return Promise.resolve(response.data);
    } else {
      return Promise.reject(response.data);
    }
  },
  (err) => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = "";
          break;
        case 401:
          err.message = "";
          //   userModel.goLogin();
          break;
        case 403:
          err.message = "";
          break;
        case 404:
          err.message = ",";
          break;
        case 405:
          err.message = "";
          break;
        case 408:
          err.message = "";
          break;
        case 500:
          err.message = err.response.data.message;
          break;
        case 501:
          err.message = "";
          break;
        case 502:
          err.message = "";
          break;
        case 503:
          err.message = "";
          break;
        case 504:
          err.message = "";
          break;
        default:
          err.message = `${err.response.msg}`;
      }
    } else {
      err.message = "";
    }
    return Promise.reject(err.response);
  }
);

export default {
  //get
  get<T = any>(
    url: string,
    param?: any,
    config?: AxiosRequestConfig
  ): Promise<Result<T>> {
    return axios({
      method: "get",
      url,
      params: param || {},
      ...config,
    } as any);
  },
  //post
  post<T = any>(
    url: string,
    param: any,
    config?: AxiosRequestConfig
  ): Promise<Result<T>> {
    return axios({
      method: "post",
      url,
      data: param || {},
      ...config,
    });
  },
  postFormData<T = any>(
    url: string,
    param: any,
    config?: AxiosRequestConfig
  ): Promise<Result<T>> {
    return axios({
      method: "post",
      url,
      ...config,
      headers: {
        ...(config?.headers || ({} as any)),
        "Content-Type": "application/x-www-form-urlencoded",
      },
      data: QS.stringify(param) || {},
    });
  },
};
