/**
 * api
 */
import $axios from "@/service/httpServer";

// 
export const getMyPages = (params?: any) =>
  $axios.get("/luckyh5/page/getMyPages", params);

// 
export const createPage = (params: any) =>
  $axios.post("/luckyh5/page/createPage", params);

// 
export const updatePage = (params: any) =>
  $axios.post("/luckyh5/page/updatePage", params);

// 
export const deletePage = (params: any) =>
  $axios.post("/luckyh5/page/deletePage", params);

// 
export const copyPage = (params: any) =>
  $axios.post("/luckyh5/page/copyPage", params);

// 
export const getPageDetail = (id: string) =>
  $axios.get("/luckyh5/page/getPageDetail", { id });

// 
export const setTemplate = (params: any) =>
  $axios.post("/luckyh5/page/setTemplate", params);

// 
export const publishPage = (params: any) =>
  $axios.post("/luckyh5/page/setPublish", params);

// 
export const getMyTemplates = (params: any) =>
  $axios.get("/luckyh5/page/getMyTemplates", params);

// 
export const getPublishTemplates = (params: any) =>
  $axios.get("/luckyh5/page/getPublishTemplates", params);
